// delete user

export const deleteUser = async (req, res) => {
  try {
    const user = req.user;

    if (!user) {
      errorResponse(res, 404, "User not found");
    }

    const deletedUser = await User.findByIdAndUpdate(user._id);

    if (deletedUser) {
      successResponse(res, 200, "User account deleted successfully");
    } else {
      errorResponse(res, 400, "Failed to delete user account");
    }
  } catch (error) {
    console.error(error);
    errorResponse(res, 500, "Internal Server Error");
  }
};
